import {gridToPos, posToGrid} from '../tools/gridPosConvertion';
import Event from '../js/event';
import CONSTANTS from '../js/constants';

let initialBoard = [];
let boardData = [];
let words = [];
let highlightPos = [];
let wordProgress = [];

export default function Board(type, data){
  if(type === "get"){
    if(!data){
      return boardData;
      // returns the current boardData if no data supplied
    }else if(data.x && data.y){
      return boardData[data.x][data.y];
      // returns the pointed board tile value
      // data: {x: .., y, ..}
    }else if(data.grid){
      return boardData[gridToPos(data.grid).x][gridToPos(data.grid).y];
      // data: {grid: ..}
    }
  }else if(type === "set"){
    initialBoard = JSON.parse(JSON.stringify(data.board));
    boardData = data.board;
    words = data.words;
    return true;
    // data supplied for set must be an array
  }else if(type === "update"){
    if(data.x && data.y){
      if(!data.reset){
        boardData[data.x][data.y] = data.value;
      }else{
        boardData[data.x][data.y] = initialBoard[data.x][data.y];
      }
    }else if(data.grid){
      if(!data.reset){
        if(boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] === "0"){
          boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] = data.value;
        }
      }else{
        boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] = initialBoard[gridToPos(data.grid).x][gridToPos(data.grid).y];
      }
    }
    checkAndUpdateHighlights(boardData);
    return true;
    // data supplied for update must be an obj
    // data: {x: .., y: .., value: ..}
  }else if(type === "hint"){
    let i = 0;
    while(words[i]){
      if(!wordProgress.contains(words[i])){
        return words[i][0];
      }
      i++;
    }
  }
}

function checkAndUpdateHighlights(currentBoardData){
  // Use event with highlight key for new changes.
  // use highlightCache :/
  let highlightCache = [];
  let wordProgressCache = [];
  words.forEach(word => {
    highlightCache.push([]);
  });

  words.forEach((word, i) => {
    let wordPos = checkWordInBoard(word, currentBoardData);
    if(wordPos){
      highlightCache[i] = wordPos;
      wordProgressCache.push(word);
    }
  });

  if(JSON.stringify(highlightCache) !== JSON.stringify(highlightPos)){
    let ifSuccessSound = getLength(highlightPos) < getLength(highlightCache);
    highlightPos = JSON.parse(JSON.stringify(highlightCache));
    if(1){
      console.log(highlightPos);
      Event.emit("highlight", highlightPos);
      if(CONSTANTS.SOUND && ifSuccessSound){
        window.postMessage("success:sound");
        console.log("successs sound");
      }
      console.log("successs!!!!");
    }
  }
  if(JSON.stringify(wordProgressCache) !== JSON.stringify(wordProgress)){
    wordProgress = wordProgressCache;
    Event.emit("progress", wordProgress);
  }
}

function getLength(highlight){
  let counter = 0;
  highlight.forEach((hAr) => {
    if(hAr.length){
      counter++;
    }
  });
  return counter;
}

function uniqueWordPush(arr, stringToPush){
  if(!arr.contains(stringToPush)){
    arr.push(stringToPush);
  }
  return arr;
}


function checkWordInBoard(word, board){
  // does this word exist in the board?
  let i = 0;
  while(i < CONSTANTS.GAME_GRID){
    let j = 0;
    let columnArray = [];
    let rowArray = [];
    while(j < CONSTANTS.GAME_GRID){
      columnArray.push(board[i][j]);
      rowArray.push(board[j][i]);
      j++;
    }
    let columnString = columnArray.join("");
    let rowString = rowArray.join("");
    if(columnString.includes(word)){
      return getPosOfWord(columnString, word, i, "col");
      break;
    }
    if(rowString.includes(word)){
      return getPosOfWord(rowString, word, i, "row");
      break;
    }
    i++;
  }
  return false;
}


function getPosOfWord(string, word, index, direction){
  let start = string.indexOf(word);
  let i = 0;
  let array = [];
  while(i < word.length){
    if(direction == "col"){
      array.push(posToGrid(start + i, index));
    }else if(direction == "row"){
      array.push(posToGrid(index, start + i));
    }
    i++;
  }
  return array;
}








